import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './Header.css';
import GnbLi from './GnbLi';  // gnb-li


function Header() {

  // const gnbLi = [
  //   { name: "Home", address: "/"},
  //   { name: "Protein", address: "/protein"},
  //   { name: "Boosters", address: "/boosters"},
  //   { name: "Vitamins", address: "/vitamins"},
  //   { name: "Event", address: "/event"},
  //   { name: "", address: "/signUp"},
  // ];



  const [click, setClick] = useState(true); // useState   click  true   setClick  click   
  const toggleClick = () => setClick(!click); //    




  const [resize, setResize] = useState(true); // header   
  const [innerWidth, setInnerWidth] = useState(window.innerWidth); // innerWidth state 
  const resizeRerender = () => { // resizererender  innerwidth  
    setInnerWidth(window.innerWidth); 
  };



  useEffect( () => { //   

    window.addEventListener('resize', resizeRerender);
    return () => {
      window.removeEventListener('resize', resizeRerender);
    }
  },[]);



  useEffect( () => { //     
    if( innerWidth <= 1200 ) { //   1200 
      setResize(false); // header   'mo'  
    } else {
      setResize(true); // 'pc'
    }
  },[innerWidth]);



  return (

    // 

    <>

    <div className="header-wrap">
      <header className={resize ? 'pc' : 'mo'}>
        {/*   true: pc, false: mo */}
        <div className="logo-wrap">
          <h1>
            <Link to='/' className='logo'></Link>
          </h1>
        </div>

        <nav className="gnb">
          <div className={click ? "gnbBtn" : "gnbBtn active"} onClick={toggleClick}>
            {/*   */}
            <span />
            <span />
            <span />
          </div>

          <div className="list-wrap">
            <ul className="gnb-ul">
              <GnbLi />
            </ul>
          </div>

        </nav>
      </header>
    </div>

    </>
  )

};

export default Header;
